import Vue from "vue";
import VueRouter from "vue-router";
import homeRoute from './home'
Vue.use(VueRouter);

const routes = [
  homeRoute,
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '登录',
      isLogin: true
    },
    component: resolve => {
      require(['@/views/login'], resolve)
    }
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router;
