export default {
    namespaced: true,
    state:{
        userInfo:null,
        isLogin:null,
    },
    getters:{
        userInfo(state){
            return state.userInfo || JSON.parse(window.localStorage.getItem('userInfo'))
        },
        isLogin(state){
            return state.isLogin || JSON.parse(window.localStorage.getItem('isLogin'));
        }
    },
    mutations:{
        setLoginStatus(state,val = false){
            state.isLogin = val;
            window.localStorage.setItem('isLogin',val);
        },
        setUserInfo(state,val = null){
            state.userInfo = val;
            window.localStorage.setItem('userInfo',JSON.stringify(val));
        }
    },
    actions:{

    }
}