import headerNav from './headerNav'
import songSheet from './songSheet'
import Banner from './banner';
import RecommendSong from './recommendSong'
import MusicPlayer from './musicPlayer'
import qsImgCmp from './songElImg'
import loading from './loading'
import SvgAnimation from './svgLoadingAndPlayAnimation'
import Button from './button'
import SongList from './songList'
import SongSheetDetailHadInfo from './songSheetDetailHadInfo'
import SongSheetList from './songSheetList'
import QsComment from './comment'
import LoadMoveBtn from './loadMoveBtn'
import MobileNav from './mobileNav'
import QsInput from './input'
const CONFIG = {
    'HeaderNav':headerNav,
    'SongSheet':songSheet,
    'Banner':Banner,
    'RecommendSong':RecommendSong,
    'MusicPlayer':MusicPlayer,
    'LoadIng':loading,
    'SvgAnimation':SvgAnimation,
    'QsImg':qsImgCmp,
    'QsButton':Button,
    'QsComment':QsComment,
    'SongList':SongList,
    "SongSheetDetailHadInfo":SongSheetDetailHadInfo,
    'SongSheetList':SongSheetList,
    'LoadMoveBtn':LoadMoveBtn,
    'MobileNav':MobileNav,
    'QsInput':QsInput
}

const CMP = {
    install(vue, options) {
        Object.keys(CONFIG).forEach(key => {
        vue.component(key, CONFIG[key])
        })
    }
}


export default CMP