<!--  -->
<template>
  <div class='singer-detail'>
    <div class="artist-info">
        <div class="background-blur">
            <QsImg class="background" :src='singerInfo.cover'/>
        </div>
        <div class="container">
            <div class="avatar-img">
                <QsImg class="img" :src='singerInfo.cover'/>
            </div>
            <div class="avatar-name">
                <div class="singer-name">
                    {{singerInfo.name}}
                    <QsImg class="tag-img" v-if="identify" :src='identify.imageUrl' />
                </div>

            </div>
            <div v-if="identify" class="tags">
                <span class="item">
                    {{identify.imageDesc}}
                </span>
            </div>
            <div class="avatar-count">
                <span class="item">单曲：{{singerInfo.musicSize}}</span>
                <span class="item">专辑：{{singerInfo.albumSize}}</span>
                <span class="item">MV：{{singerInfo.mvSize}}</span>
            </div>
            <div class="desc ellipsis">
                {{singerInfo.briefDesc}}
            </div>
            <div class="btn-group">
                <QsButton icon='el-icon-plus' class="btn" text='关注歌手' />
                <QsButton class="btn" text='个人主页' />
            </div>
        </div>
    </div>
    <div class="singer-container">
        <div class="title">
            热门单曲
        </div>
        <SongList :showHeaderInfo='false' :type='3' :data='{playList:hotSongs}' />
        <div class="title">
            MV
        </div>
        <VideoList :videoList='singerMv' />
    </div>
  </div>
</template>

<script>
import { formatSongInfo } from '@/utils/song.js'
import VideoList from '../../video/main/video-list'
export default {
  components: {VideoList},
  inject:['reload'],
  data() {
    return {
        singerInfo:{},
        identify:{},
        hotSongs:[],
        singerMv:[]
    }
  },
  // 监听属性 类似于data概念
  computed: {
    singerId(){
        return this.$route.params.id
    }
  },
  // 监控data中的数据变化
  watch: {
    singerId(){
        this.reload();
    }
  },
  //方法集合
  methods: {
    // 获取歌手详细信息
    async getSingerDetail(){
        let res = await this.$api.getSingerDetail(this.singerId);
        this.singerInfo = res.data['artist'];
        this.identify = res.data.identify;
    },
    //获取歌手热门歌曲
    async getSingerHotSongs(){
        let res = await this.$api.getSingerHotSongs(this.singerId);
        res.songs.forEach(elem => {
            this.hotSongs.push(formatSongInfo(elem));
        });
    },
    //获取歌手MV
    async getSingerMv(){
        let res = await this.$api.getSingerMv(this.singerId);
        this.singerMv = res.mvs;
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getSingerDetail();
    this.getSingerHotSongs();
    this.getSingerMv();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
}
</script>
