<!--  -->
<template>
    <div class='songsheetlist-main'>
        <div class="songSheets">
            <div v-for="(list,index) in songSheetList" :key="index" class="list">
                <router-link :to="`/songsheet/songsheetdetail/${list.id}`">
                    <div class="songsheet-img">
                        <QsImg class="img" :src='list.coverImgUrl+"?param=240y240"' />
                        <QsImg class="btn-img" :src='require("@/assets/img/btn-play.png")' />
                        <div :title="list.playCount" class="hot-msg">
                            <i class="iconfont icon-erji"></i>
                            {{utils.tranNumber(list.playCount,2)}}
                        </div>
                        <QsImg class="avatar" :src='list.creator.avatarUrl+"?param=70y70"'/>
                    </div>
                </router-link>
                <div class="name ellipsis-two">
                    <router-link :title="list.name" :to="`/songsheet/songsheetdetail/${list.id}`"> {{list.name}}</router-link>
                </div>
                <div class="creator ellipsis">
                    by&nbsp; <router-link class="creator-name" :title="list.creator.nickname" to="/">
                        {{list.creator.nickname}}
                        <QsImg v-if="list.creator.avatarDetail" class="creator-title" :src='list.creator.avatarDetail.identityIconUrl+"?param=70y70"'/>
                    </router-link>
                </div>
            </div>
        </div>
        <div class="pagination">
            <el-pagination
                @current-change='curChange'
                :page-size='params.limit'
                background
                layout="prev, pager, next"
                :total="songSheetCount"
                :current-page="(params.offset/params.limit)+1"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            params:{
                type:Object,
                default:() => {}
            }
        },
        data() {
            return {
                songSheetList:[],
                songSheetCount:0
            }
        },
        // 监听属性 类似于data概念
        computed: {},
        // 监控data中的数据变化
        watch: {
            params:{
                handler(val, oldVal){
                    this.loadSongSheetData(val)
                },
                deep:true
            }
        },
        //方法集合
        methods: {
            async loadSongSheetData(params){
                let res = await this.$api.getHotSongSheet(params);
                this.songSheetList = res.playlists;
                this.songSheetCount = res.total;
            },
            curChange(offset){
                console.log(offset)
                this.$emit('changeOffset',(offset-1)*30);
            }
        },
        // 生命周期 - 创建完成（可以访问当前this实例）
        created() {
            this.loadSongSheetData(this.params);
        },
        //生命周期 - 挂载完成（可以访问DOM元素）
        mounted() {},
    }
</script>
