<template>
  <div class='songsheet-list'>
    <div
        @click="changeToprank(list.id,index)"
        :class="topActiveNum == list.id  ? 'active' : '' "
        v-for="(list,index) in songSheetList"
        :key="index"
        class="top-list"
    >
        <div class="img">
        <QsImg style="width:100%;" :src='list.coverImgUrl+"?param=60y60"' :lazy='false' />
        </div>
        <div class="text">
        <p>{{list.name}}</p>
        <p v-if="list.updateFrequency && type == 1">{{list.updateFrequency}}</p>
        </div>
    </div>
  </div>
</template>

<script>

export default {
    name:'songSheetList',
    props:{
        songSheetList:{
            type:Array,
            default:() => []
        },
        topActiveNum:{
            type:Number,
            default:0
        },
        type:{
          type:Number,
          default:1
        }
    },
    data() {
        return {
        }
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        changeToprank(id,index){
            this.$emit('changeToprank',id,index);
        }
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {},
}
</script>
