import Vue from "vue";
import Vuex from "vuex";
import music from './music/index'
import user from './user/index'
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    showMobile:false,
    showSearch:false,
    searchVal:''
  },
  getters:{
    showMobile(state){
      return state.showMobile;
    },
    showSearch(state){
      return state.showSearch;
    },
    searchVal(state){
      return state.searchVal;
    }
  },
  mutations: {
    changeMobileShow(state,show = undefined){
      state.showMobile = show === undefined ? !state.showMobile : show;
    },
    changeSearchShow(state,show = undefined){
      state.showSearch = show === undefined ? !state.showSearch : show;
    },
    changeSearchVal(state,val){
      state.searchVal = val;
    }
  },
  actions: {},
  modules: {
    music,
    user
  }
});
