import Home from "../views/Home.vue";
//发现音乐
import findMusic from '../views/findMusic'
//排行榜
import rankingList from '../views/rankingList'
//我的音乐
import myMusic from '../views/myMusic'
//歌单
import songSheetApp from '../views/songSheetMain/app.vue'
import songSheetMain from '../views/songSheetMain'
import songSheetDetail from '../views/songSheetMain/detail'
// 视频
import videoApp from '../views/video'
import videoMain from '../views/video/main'
import videoDetail from '../views/video/detail'
// 歌手
import singerApp from '../views/singer'
import singerMain from '../views/singer/main'
import singerDetail from '../views/singer/detail'
const route = {
    path: "/",
    redirect:'/findmusic',
    name: "Home",
    component: Home,
    children:[
      {
        path:'findmusic',
        name:'findmusic',
        component:findMusic
      },
      {
        path:'ranking',
        name:'ranking',
        component:rankingList
      },
      {
        path:'mymusic',
        name:'myMusic',
        component:myMusic
      },
      {
        path:'songsheet',
        redirect:'/songsheet/songsheetmain',
        name:'songsheet',
        component:songSheetApp,
        children:[
          {
            path:'songsheetmain',
            name:'songsheetmain',
            component:songSheetMain,
          },
          {
            path:'songsheetdetail/:id',
            name:'songSheetDetail',
            component:songSheetDetail
          }
        ]
      },
      {
        path:'video',
        redirect:'/video/videomain',
        name:'video',
        component:videoApp,
        children:[
          {
            path:'videomain',
            name:'videomain',
            component:videoMain
          },
          {
            path:'videodetail/:id',
            name:'videodetail',
            component:videoDetail
          },
        ]
      },
      {
        path:'singer',
        name:'singer',
        redirect:'/singer/singermain',
        component:singerApp,
        children:[
          {
            path:'singermain',
            name:'singerMain',
            component:singerMain
          },
          {
            path:'detail/:id',
            name:'singerDetail',
            component:singerDetail
          },
        ]
      }
    ],

  }

  export default route