                <div class="rank">
                    <a>
                        <SvgAnimation v-if="curPlayInfo && item.id == curPlayInfo.id && isPlay" type='3' color='#fff' />
                        <i @click="playSong(item)" v-else-if="item.id != curPlayInfo.id || !isPlay" :class="['icon-bofang']" class="iconfont "></i>
                        <!-- <i @click="playSong(item)" :class="['icon-bofang']" class="iconfont "></i> -->
                    </a>

                </div>
                <div class="song-msg">
                    <div class="songmsg-container">
                        <div class="img">
                            <QsImg
                                style="max-height:60px;"
                                :src="item.album.picUrl+ '?param=150y150'"
                            />
                        </div>
                        <div class="msg">
                            <div class="ellipsis">{{item.name}}</div>
                            <div class="ellipsis">{{item.singer[0].name}}</div>
                        </div>
                    </div>
                </div>
                <div class="zj">
                    <div class="albumname ellipsis">《{{item.album.name}}》</div>
