<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
export default {
    props:{
        title:{
            type:String,
            default:'新歌速递'
        },
        datas:{
            type:Array,
            default:() => []
        }
    },
    components: {},
    data() {
        return {}
    },
    // 监听属性 类似于data概念
    computed: {
        ...mapGetters('music',[
            'isPlay',
            'playList',
            'playIndex'
        ]),
        curPlayInfo(){
            return this.playIndex != null ? this.playList[this.playIndex] || {} : {}
        },
    },
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        ...mapActions('music',[
            'playChoiceSong'
        ]),
        playSong(data){
            console.log(data.id)
            this.playChoiceSong({list:[data]});
        },
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {
    // console.log(this.datas)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        console.log(this)
    },
}
</script>
