        <div v-for="(list,index) in songSheetList.slice(0,5)" :key="index" class="list">
            <router-link :to="`/songsheet/songsheetdetail/${list.id}`">
                <div class="songsheet-img">
                    <QsImg class="img" :src='list.coverImgUrl+"?param=240y240"' />
                    <QsImg class="btn-img" :src='require("@/assets/img/btn-play.png")' />
                </div>
            </router-link>
            <div class="name ellipsis-two">
                <router-link :title="list.name" :to="`/songsheet/songsheetdetail/${list.id}`"> {{list.name}}</router-link>
            </div>
            <div class="creator ellipsis">
                by&nbsp; <router-link class="creator-name" :title="list.creator.nickname" to="/">
                    {{list.creator.nickname}}
                </router-link>
            </div>
        </div>
    </div>
