</template>

<script>

export default {
    props:{
        id:{
            type:String,
            default:''
        }
    },
    data() {
        return {
            songSheetList:[]
        }
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        async getrelatedSongSheetData(){
            let res = await this.$api.getrelatedSongSheet(this.id);
            this.songSheetList = res.playlists;
        }
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.getrelatedSongSheetData();
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {},
}
</script>
