<!--  -->
<template>
  <div class='ranking-list'>
    <el-row class="container">
      <el-col class="left-top"  :xl='6' :lg='6' :md='6' :sm='24' :xs='24'>
        <div style="font-size:14px;padding:0 5px 10px;" class="homecmp-title">
          排行榜单
        </div>
        <LoadIng type='4' v-if="topDataLoading" />
        <transition name="el-zoom-in-top">
          <SongSheetList v-if="!topDataLoading" :songSheetList='topData' :topActiveNum='topActiveNum' @changeToprank='changeToprank' />
        </transition>
      </el-col>
      <el-col class="right-rankinfo" :xl='18' :lg='18' :md='18' :sm='24' :xs='24'>
        <LoadIng class="loading" type='4' v-if="rightInfoLoading" />
        <transition name="el-zoom-in-top">
          <div v-show="!rightInfoLoading">
            <SongSheetDetailHadInfo :subscribed='subscribed' :curTopRankData='curTopRankData' />
            <SongList style="padding:0 40px;box-sizing: border-box;" :data='curTopRankData'/>
          </div>
        </transition>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  components: {
  },
  name:'rankinglist',
  data() {
    return {
      //榜单列表数据
      topData:[],
      topDataLoading:true,
      topActiveNum:null,
      //歌单详情数据
      curTopRankData:{},
      //右侧loading控制
      rightInfoLoading:true,
      //歌单是否收藏
      subscribed:false
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {
  },
  //方法集合
  methods: {
    //获取榜单列表
    async getData(){
      this.topDataLoading = true;
      let res = await this.$api.getAllTopRank();
      this.topDataLoading = false;
      this.topData = res.list;
      this.changeToprank(this.topData[0].id,0);
    },
    //获取排行榜详情
    async changeToprank(id,index){
      if(this.topActiveNum == id) return;
      document.querySelector('.home-container').scrollTop = 0;
      this.topActiveNum = id;
      this.rightInfoLoading = true;
      let res = await this.$api.getSongSheetDetail(id);
      this.getSongSheetDynamic(id);
      this.rightInfoLoading = false;
      this.curTopRankData = res.playlist;
    },
    //获取歌单动态 是否收藏
    async getSongSheetDynamic(id){
      let res = await this.$api.getSongSheetDynamic(id);
      this.subscribed = res.subscribed;
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getData();
    console.log((new Date(1635986397285)).getMonth())
    console.log(String(12).length)
    console.log((Array(2).join(0) + 12).slice(1))
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
}
</script>
