<!--  -->
<template>
  <div class='progressive-bar' @click.stop="point" ref="progress">
    <div :style="{width:`${progressNum}%`}" class="progressive">
        <div @mousedown="move" class="progressive-btn">
        </div>
    </div>
  </div>
</template>

<script>
export default {
    props:{
        progressNum:{
            type:Number,
            default:0,
        }
    },
    components: {},
    data() {
        return {}
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        move(e){
            console.log(e)
             const self = this
            document.onmousemove = (e) => {
                self.setProgressLine(e, false)

            }
            // 拖拽进度条的时候 禁止页面文字选中
            document.body.onselectstart = function () { return false }
            document.onmouseup = function (e) {
                // 拖拽结束时候 设置当前音频时间
                self.setProgressLine(e)
                console.log(document.onmouseup)
                document.onmousemove = document.onmouseup = document.body.onselectstart = null
            }
            return false
        },
        // 点击音频进度条
        point (e) {
            console.log(e)
            this.setProgressLine(e)
        },
        setProgressLine (e, flag = true) {
            const self = this
            const $progress = self.$refs.progress
            // console.log($progress.offsetLeft);
            // console.log($progress.offsetParent.offsetLeft);
            console.log($progress.offsetWidth)
            //  实时获取鼠标横坐标，若是超过进度条最大宽度，则值为进度条的宽度

            const curProgress = e.clientX - this.getOffsetLeft($progress) >= $progress.offsetWidth ? $progress.offsetWidth : (e.clientX - this.getOffsetLeft($progress) <= 0 ? 0 : e.clientX - this.getOffsetLeft($progress))

            self.$emit('setProgressLine', { val: curProgress / $progress.offsetWidth, flag: flag })
        },
        // 获取元素到浏览器左侧距离
        getOffsetLeft (obj) {
            let oLeft = obj.offsetLeft
            let oParent = obj.offsetParent

            while (oParent !== null) {
                oLeft += oParent.offsetLeft
                oParent = oParent.offsetParent
            }
            console.log(oLeft)

            return oLeft
        }
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {},
}
</script>
<style lang='less' scoped>
//@import url(); 引入公共css类
    .progressive-bar{
        cursor: pointer;
        // position: absolute;
        // top: -7px;
        // left: -40px;
        width: 100%;
        height: 7px;
        background: #a4b0be;
        .progressive{
            position: relative;
            max-width: 100%;
            width: 0;
            height: 7px;
            background: #eccc68;
            .progressive-btn{
                position: absolute;
                right:-7px;
                top: -4px;
                background: #ff6348;
                width: 16px;
                height: 16px;
                border-radius: 50%;
                cursor: grab;
            }
        }
    }
</style>