<template>
  <div class='find-music'>
    <div class="container">
      <Banner :bannerData='bannersList' slidesPerView='3' />
      <div class="spacing"></div>
      <SongSheet :loading='songSheetsLoading' @songSheetCatChange='getHoutSongSheet' title="热门歌单" :songSheetDataList='songSheets' />
      <RecommendSong :datas='newSongs' />
    </div>
  </div>
</template>

<script>
import { formatSongInfo }  from '../../utils/song';
export default {
  name:'findmusic',
  data() {
    return {
      bannersList:[],
      songSheets:[],
      songSheetsLoading:true,
      newSongs:[],
      iterator:null
    }
  },
  // 监听属性 类似于data概念
  computed: {
  },
  // 监控data中的数据变化
  watch: {
    newSongs(){

    }
  },
  //方法集合
  methods: {
    async getBanner() {
      try {
        let res = await this.$api.getBanner()
        this.bannersList = res.banners
      } catch (error) {
      }
    },
    getHoutSongSheet(cat = '全部'){
      this.songSheetsLoading = true;
      this.$api.getHotSongSheet({limit:48,cat}).then(res =>{
        if(res.code == 200){
          this.songSheets = res.playlists;
        }
        this.songSheetsLoading = false;
      });
    },
    getNewSong(){
      this.$api.getNewSongs(10).then(res =>{
        console.log(res)
        console.log(this.iterator.next(res.result))
        this.iterator.next(res.result);

      })
    },
     SongFormat(datas){
      console.log(datas)
        let arr = datas;
        let ids = '';
        arr.forEach((elem,index) => {
          console.log(ids)
            ids+= `${elem.id}${index == arr.length-1 ? '' : ','}`;
            console.log(ids)
        });

        // console.log(this.$api.getSongsDetail(ids).then(console.log(({songs}))))
        this.$api.getSongsDetail(ids).then(({songs}) =>{
          console.log("到我了")
            let result = [];
            console.log(songs)
            songs.forEach(elem => {
                result.push(formatSongInfo(elem));
            });
            this.newSongs = result;
            this.iterator.next(result);
            console.log(this.newSongs)
        });

    },
    * getNewSongFormat(){
      console.log("先到我")
      let datas = yield this.getNewSong();
      console.log("再到我")
      yield this.SongFormat(datas);
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.iterator = this.getNewSongFormat();
    console.log("****************")
    this.iterator.next();
    console.log("??????????")
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.getBanner();
    this.getHoutSongSheet();
    this.getNewSong();
  },
}
</script>
