<template>
  <div :class="[bgcStyle]" class='header-nav'>
    <div class="nav-container">
        <div class="left">

        </div>
        <div class="item-box">
            <div
            :class="{'item-active':activeIndexNum == index}"
            @click="changeNavAndGoToRoute(item,index)"
            :ref="item.routePath" v-for="(item,index) in navs"
            :key="index"
            class="nav-item"
        >
            {{item.name}}
            </div>
        </div>
        <el-dropdown @command="handleCommand" >
            <div :title="isLogin ? userInfo.artistName : '请登录' " class="right-head">
                <QsImg
                    :lazy='false'
                    style="width:100%;height:100%;margin-bottom:6px;"
                    :src=" isLogin ? userInfo.avatarUrl : require('@/assets/img/headImg.png')"
                />
            </div>
            <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command='userName' >{{isLogin ? userInfo.nickname : '请登录账号'}}</el-dropdown-item>
                <el-dropdown-item v-if="isLogin" command='我的主页'  divided>我的主页</el-dropdown-item>
                <el-dropdown-item v-if="isLogin" command='我的消息' >我的消息</el-dropdown-item>
                <el-dropdown-item v-if="isLogin" command='个人设置' >个人设置</el-dropdown-item>
                <el-dropdown-item v-if="isLogin" command='退出' >退出</el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
        <div :style="activeStyle" class="nav-activeline">
        </div>
    </div>
    <div class="mobile-nav">
        <div :class="[show]" @click.stop="$store.commit('changeMobileShow')" class="left-menu">
            <!-- <i @click.stop="$store.commit('changeMobileShow')" class="iconfont icon-liebiao1"></i> -->
            <div class="bar">
            </div>
            <div class="bar">
            </div>
            <div class="bar">
            </div>
        </div>
        <div class="mid-serach">
            <QsInput @focus='changeSearchShow(true)' />
        </div>
        <div class="right-avatar">
            <el-dropdown trigger="click" @command="handleCommand" >
                <QsImg
                    class="avatar"
                    :lazy='false'
                    :src=" isLogin ? userInfo.avatarUrl : require('@/assets/img/headImg.png')"
                />
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command='userName' >{{isLogin ? userInfo.nickname : '请登录账号'}}</el-dropdown-item>
                    <el-dropdown-item v-if="isLogin" command='我的主页'  divided>我的主页</el-dropdown-item>
                    <el-dropdown-item v-if="isLogin" command='我的消息' >我的消息</el-dropdown-item>
                    <el-dropdown-item v-if="isLogin" command='个人设置' >个人设置</el-dropdown-item>
                    <el-dropdown-item v-if="isLogin" command='退出' >退出</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  data() {
    return {
        navs:[
            {name:'发现音乐',routePath:'findmusic',routeName:'findmusic'},
            {name:'排行榜',routePath:'ranking',routeName:'ranking'},
            {name:'歌单',routePath:'songsheet',routeName:'songsheetmain'},
            {name:'视频',routePath:'video?cat=全部&type=全部',routeName:'video'},
            {name:'歌手',routePath:'singer?area=-1&type=-1&initial=-1',routeName:'singer'},
            {name:'我的音乐',routePath:'mymusic',routeName:'mymusic'},
            // {name:'动态',routePath:'dynamic',routeName:'dynamic'},
            // {name:'电子冲脉',routePath:'electronpulse',routeName:'electronpulse'}
        ],
        activeStyle:{
            left:0,
            width:0
        },
        activeIndexNum:0
    }
  },
  // 监听属性 类似于data概念
  computed: {
    ...mapGetters('user',[
        'userInfo',
        'isLogin'
    ]),
    bgcStyle(){
        if(this.$route.name == 'videodetail')
        return 'bgc-1';
    },
    show(){
        return this.$store.getters.showMobile ? 'active' : '';
    }
  },
  // 监控data中的数据变化
  watch: {

  },
  //方法集合
  methods: {
    ...mapMutations('user',[
      'setLoginStatus',
      'setUserInfo',
    ]),
    ...mapMutations([
        'changeSearchShow'
    ]),
    changeNavAndGoToRoute(data,activeNum){
        this.activeIndexNum = activeNum;
        let {offsetWidth, offsetLeft} = this.$refs[data.routePath][0];
        this.$set(this.activeStyle,'width',`${offsetWidth}px`);
        this.$set(this.activeStyle,'left',`${offsetLeft}px`);
        if(this.$route.name != data.routeName)
        return this.$router.push(`/${data.routePath}`);
    },
    handleCommand(command){
        switch (command) {
            case '退出':
                this.$confirm('是否退出登录?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    window.localStorage.removeItem('cookie');
                    window.localStorage.removeItem('token');
                    window.localStorage.removeItem('loginStatue');
                    this.setLoginStatus();
                    this.setUserInfo();
                    this.$api.logout().then(res =>{
                        if(res.code == 200){
                            this.$message({
                                type: 'success',
                                message: '已成功退出登录!'
                            });
                        }
                    });
                });
                break;
            case 'userName':
                if(this.isLogin)
                return;
                this.$router.push('/login');
                break;
            default:
                break;
        }
    }

  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
      console.log(window.localStorage)
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.activeIndexNum = this.navs.findIndex(val => this.$route.name == val.routeName);
    this.$nextTick(() =>{
        let {offsetWidth, offsetLeft} = this.$refs[this.navs[0].routePath][0];
        this.$set(this.activeStyle,'width',`${offsetWidth}px`);
        this.$set(this.activeStyle,'left',`${offsetLeft}px`);
    })
  },
}
</script>
