<template>
    <div class='songlist'>
        <div v-if="showHeaderInfo" class="header-info">
            <div class="left-container">
                <span class="homecmp-title">
                    歌曲列表
                </span>
                <span class="song-count">{{data.trackCount}}首歌</span>
            </div>
            <div class="right-container">
                播放：<span style="color:#c20c0c;">{{data.playCount}}</span>&nbsp;次
            </div>
        </div>
        <div v-if="type !=3" style="width:100%;height:2px;background:#ff6348;"></div>
        <div class="songlist-table">
            <div v-if="type !=3" class="title">
                <div class="col num">{{tipsNum}}</div>
                <div class="col name">歌曲名称</div>
                <div class="col time">时长</div>
                <div class="col singer">歌手</div>
            </div>
            <div :class="[list.vip ? 'vip': '']" :style="{'line-height':`${lineHeight}px`}" v-for="(list,index) in songList" :key="index" class="song-list">
                <div class="col num">
                    <SvgAnimation style="transform: rotate(180deg);" v-if="curPlayInfo && list.id == curPlayInfo.id && isPlay" type='3' color='#bfa' />
                    <span v-else>{{index+1}}</span>
                </div>
                <div class="col name">
                    <QsImg v-if="index <= 2 && type == 1" style="width:50px;" :src='list.album.picUrl+"?param=60y60"' :lazy='false' />
                    <i
                        @click="playChoiceSong({list:[list]})"
                        :class="[curPlayInfo && list.id == curPlayInfo.id && isPlay ? 'el-icon-video-pause' : 'el-icon-video-play']"
                        class="icon"
                    >

                    </i>
                    <div class="song-name ellipsis">
                        <a :title="list.name">{{list.name}}</a>
                        <i v-if="list.vip" title="vip歌曲" class="vipicon iconfont icon-VIP"></i>
                        <router-link :to="`/video/videodetail/${list.mvId}`"> <i v-if="list.mvId !== 0" title="mv" class="mvicon iconfont icon-Movies"></i></router-link>
                    </div>
                    <div v-if="showoOperation" class="operation animate__animated animate__fadeInRight">
                        <i @click="addSong({list:[list]})" title="添加到播放列表" class="icon el-icon-plus"></i>
                        <i title="收藏至歌单" data-v-5a8dd545="" class="icon el-icon-star-off"></i>
                        <i title="分享单曲" data-v-5a8dd545="" class="icon el-icon-share"></i>
                    </div>
                    <div v-if="!showoOperation" class="operation animate__animated animate__fadeInRight">
                        <i @click="removePlayListSong(index)" title="从播放列表中删除" class="icon el-icon-close"></i>
                    </div>
                </div>
                <div class="col time">{{list.duration}}</div>
                <div class="col singer">
                    <div class="ellipsis">
                        <span @click="$router.push(`/singer/detail/${singerItem.id}`)" v-for="(singerItem,singerIndex) in list.singer" :key="singerIndex">
                            {{singerItem.name}}
                            {{singerIndex == (list.singer.length - 1) ? '' : '/'}}
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { formatSongInfo } from '@/utils/song.js'
export default {
    props:{
        data:{
            type:Object,
            default:() => {}
        },
        tipsNum:{
            type:String,
            default:'排名'
        },
        showoOperation:{
            type:Boolean,
            default:true
        },
        type:{
            type:Number,
            default:1
        },
        showHeaderInfo:{
            type:Boolean,
            default:true
        },
        lineHeight:{
            type:Number,
            default:40
        }
    },
    components: {},
    data() {
        return {}
    },
    // 监听属性 类似于data概念
    computed: {
        ...mapGetters('music',[
            'playList',
            'playIndex',
            'isPlay'
        ]),
        curPlayInfo(){
            return this.playList[this.playIndex]
        },
        songList(){
            console.log(this)
            if(this.type == 3)
            return this.data.playList;
            let result = [];
            if(this.data.tracks){
                this.data.tracks.forEach(elem => {
                    result.push(formatSongInfo(elem))
                });
            }
            console.log(result)
            return result;
        }
    },
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        ...mapActions('music',[
            'playChoiceSong',
            'removePlayListSong',
            'addSong'
        ]),
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {
        console.log(this)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
    },
}
</script>
