<template>
  <div class='songsheet-main'>
    <div class="container">
        <ClassCat @postCat='postCat' />
        <div class="title-content">
            <div class="title">
                全部歌单
            </div>
            <div class="btn-group">
                <div @click="hotAndnewChange('hot')" :class="[params.order == 'hot' ? 'active' : '']" class="btn">热门</div>
                <div @click="hotAndnewChange('new')" :class="[params.order == 'new' ? 'active' : '']" class="btn">上新</div>
            </div>
        </div>
        <SongSheetMain @changeOffset='val => params.offset=val' :params='params' />
    </div>
  </div>
</template>

<script>
import ClassCat from './classification'
import SongSheetMain from './songSheetList-Main'
export default {
    components: {
        ClassCat,
        SongSheetMain
    },
    data() {
        return {
            params:{
                order:'hot',
                cat:this.$route.query.cat ||  '全部',
                offset:0,
                limit:30
            }
        }
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        postCat(val){
            this.$set(this.params,"cat",val);
            this.$set(this.params,"offset",0);
            console.log(this.params.offset);
        },
        hotAndnewChange(val){
            this.$set(this.params,"order",val);
            this.$set(this.params,"offset",0);
        }
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
}
</script>
