<!--  -->
<template>
  <div class='video-main'>
    <div class="container">
      <div class="video-cat">
        <div class="classification-title">
          地区
        </div>
        <ul class="cats">
          <li v-for="(item,index) in cats" :key="index" class="cat">
            <router-link :title="item" :to="{path:'/video/videomain',query:{cat:item,type:$route.query.type}}"> {{item}}</router-link>
          </li>
        </ul>
        <div class="classification-title">
          分类
        </div>
        <ul class="cats">
          <li v-for="(item,index) in types" :key="index" class="cat">
            <router-link :title="item" :to="{path:'/video/videomain',query:{cat:$route.query.cat,type:item}}"> {{item}}</router-link>
          </li>
        </ul>
      </div>
      <div class="video-container">
        <div class="title">
          全部视频
        </div>
        <Videos :videoList='videos' />
        <LoadMoveBtn :move='more' @click.native="loadMove" :loading='loading' />
      </div>
    </div>
  </div>
</template>

<script>
import Videos from './video-list'
export default {
  components: {
    Videos
  },
  data() {
    return {
      cats:['全部','内地','港台','欧美','日本','韩国'],
      types:['全部','官方版','原生','现场版','网易出品'],
      videos:[],
      limit:18,
      offset:0,
      total:0,
      loading:false,
      more:null
    }
  },
  // 监听属性 类似于data概念
  computed: {
    params(){
      let params = {
        area:this.$route.query.cat,
        type:this.$route.query.type,
        limit:this.limit,
        offset:this.offset * this.limit
      }
      return params;
    }
  },
  // 监控data中的数据变化
  watch: {
    'params.area'(){
      this.offset = 0;
      this.getAllMvList();
    },
    'params.type'(){
      this.offset = 0;
      this.getAllMvList();
    },
  },
  //方法集合
  methods: {
    //获取所有mv
    async getAllMvList(loadmove = false){
      this.loading = true;
      let res = await this.$api.getAllMv(this.params);
      console.log(res)
      console.log(this.params)
      this.loading = false;
      this.total = res.count;
      this.more = res.hasMore;
      if(loadmove)
      return this.videos = this.videos.concat(res.data);
      this.videos = res.data;
    },
    loadMove(){
      if(!this.more)
      return;
      ++this.offset;
      this.getAllMvList(true);
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getAllMvList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    window.addEventListener('scroll',() =>{
      this.utils.watchOnScroll(this.loadMove,this.loading,this.more);
    }, true);
  },
}
</script>
