<!--  -->
<template>
  <div v-if="curPlayInfo && playList!=null && playList.length>=1 && $route.name!='videodetail'" @mousemove="playermoveAndleave('move')" @mouseleave="playermoveAndleave('leave')" :class=" playerIsOpen ? 'player-open' : '' " class='music-player'>
    <ProgressiveBar @setProgressLine='setAudioProgressive' :progressNum='audioProgress' class="song-progressive" />
    <div class="container">
        <audio
        ref="audio"
        preload="auto"
        @canplay="canplaySong"
        @playing="playSong"
        @ended="endedSong"
        @error="errorSong"
        @timeupdate="updateTime"
        :src="`https://music.163.com/song/media/outer/url?id=${curPlayInfo.id}.mp3`"
        />
        <i @click="lock = !lock" class="iconfont lock" :class="lock ? 'icon-ai242' : 'icon-ai243'">
        </i>
        <div class="info">
         <div class="song-message">
            <div class="song-img">
                <img width="100%" :src="curPlayInfo.album.picUrl + '?param=70y70'" alt/>
            </div>
            <div class="song-text">
                <p :title="curPlayInfo.name" class="songname ellipsis">{{curPlayInfo.name}}</p>
                <p class="singer ellipsis">{{curPlayInfo.singer[0].name}}</p>
            </div>
         </div>
         <div class="player">
            <i @click="audioHandler('prev')" class="icon iconfont icon-prev" ></i>
            <i @click="audioHandler('play')" :class="[playIcon] " class="icon iconfont player-bofang"></i>
            <i @click="audioHandler('next')" class="icon iconfont icon-next" ></i>
         </div>
         <div class="right-info">
            <div class="song-time">
                {{utils.formatSecondTime(curPlayTime)}}
                /
                {{curPlayInfo.duration}}
            </div>
            <div class="volume">
                <i :class="[volumeIcon]" class="volume-icon iconfont" ></i>
                <ProgressiveBar @setProgressLine='setAudioVolume' :progressNum='volumeProgress' class="volume-progressive" />
            </div>
            <i @click="changePlayPattern" :title="playPatternMsg.msg" :class="[playPatternMsg.icon]" class="icon iconfont"></i>
            <el-popover
                transition='el-zoom-in-bottom'
                placement="bottom"
                width="520"
                trigger="hover"
                popper-class='playlist-popper'
            >
                <div class="container">
                    <SongList
                        :type='3'
                        :showoOperation='false'
                        :showHeaderInfo='false'
                        tipsNum='序号'
                        :data='{playList}'
                        :lineHeight='30'
                    />
                </div>
                <i slot="reference" title="播放列表" class="icon iconfont icon-liebiao1"></i>
            </el-popover>
         </div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import ProgressiveBar from '../progressive-bar';
import { log } from 'util';
export default {
  components: {
    ProgressiveBar
  },
  data() {
    return {
        lock:false,
        initAudioReady:false,//初始化音频
        curPlayTime:0,//音乐当前播放时间
        totalPlayTime:0,//音乐总时长
        playPattern:0,//播放模式 0 顺序播放 1 循环播放 2 随机播放
        playerIsOpen:false,
        timeout:null,
        volume:0.5,
        audioErrorTimeOut:null
    }
  },
  // 监听属性 类似于data概念
  computed: {
    ...mapGetters('music',[
      'isPlay',
      'playList',
      'playIndex',
    ]),
    //播放模式 0 顺序播放 1 循环播放 2 随机播放
    playPatternMsg(){//
        let params = [
            {icon:'icon-Reloade',msg:'顺序播放'},
            {icon:'icon-Loading',msg:'单曲循环'},
            {icon:'icon-Shuffle',msg:'随机播放'},
        ]
        return params[this.playPattern];
    },
    playIcon(){
        return this.isPlay ? 'icon-bofang1' : 'icon-bofang'
    },
    curPlayInfo(){
        return this.playList == null || this.playList.length <=0  ? {song:{artists:[{name:''}]}} : this.playList[this.playIndex]
    },
    audioProgress(){//音频进度条
        return this.curPlayTime / this.totalPlayTime * 100;
    },
    volumeProgress(){
        return this.volume * 100;
    },
    volumeIcon(){
        return this.volumeProgress <=0 ? 'icon-jingyin01' :'icon-yinliang'
    }
  },
  // 监控data中的数据变化
  watch: {
    //监听歌曲发生变化时
    curPlayInfo(newSong,oldSong){
        clearTimeout(this.audioErrorTimeOut);
        if(!newSong || newSong.id === oldSong.id)
        return;
        this.curPlayTime = 0;
        this.$nextTick(() => {
            const audio = this.$refs.audio;
            audio.play()
        })

    },
    //监听歌曲播放状态
    isPlay(val){
        console.log("++++------")
        console.log(this.initAudioReady)
        if(!this.initAudioReady)
        return;
        console.log(this.initAudioReady)
        console.log("+++++++++++++------")
        this.$nextTick(() => {
            const audio = this.$refs.audio;
            if(audio){
                val ? audio.play() : audio.pause()
            }
        })
    }
  },
  //方法集合
  methods: {
    ...mapMutations('music',[
        'setPlayStatus',
        'setPlayList',
        'setPlayIndex'
    ]),
    //播放器鼠标移入移出时触发
    playermoveAndleave(val){
        if(this.lock) return;
        // console.log(this)
        if(val == 'move'){
            clearTimeout(this.timeout);
            this.playerIsOpen = true;
        }else{
            this.timeout = setTimeout(() => {
                if(!this.lock)
                return this.playerIsOpen = false;
            },1000*3);
        }
    },
    //歌曲操作按钮
    audioHandler(type){
        if(type == 'play'){
            this.setPlayStatus(!this.isPlay);
            this.setPlayIndex(this.playIndex);
            console.log(this.isPlay,this.playIndex)
        }else{
            this.changPlay(type);
        }
    },
    //切换歌曲 上一首下一首 播放暂停
    changPlay(type){
        console.log("到我了----------------");
        console.log(this.playList)
        console.log(this.playIndex)
        console.log(this.curPlayInfo)
        if(this.playList.length !==1){
            let playIndex = this.playIndex;
            if(this.playPattern != 2){
                if(type == 'prev'){
                    playIndex = playIndex == 0 ? (this.playList.length-1) : --playIndex;
                    console.log("****",playIndex)
                }else{
                    playIndex = playIndex >= (this.playList.length -1) ? 0 : ++playIndex;
                    console.log("----",playIndex)
                }
            }else{ //随机播放
                playIndex = Math.round(Math.random()*(this.playList.length-1));
            }
            this.initAudioReady = false;
            this.setPlayStatus(false);
            this.setPlayIndex(playIndex);
        }else{
            this.$message.warning(`没有${type == 'next' ? "下" : '上'}一首歌啦~`);
            this.loopPlaySong(false);
        }
    },
    loopPlaySong(){
        // if(resetCurTime){
            this.curPlayTime = 0;
        // }
        const audio = this.$refs.audio;
        audio.play();
        this.setPlayStatus(true);
    },
    // 解决刷新页面时候，音频准备就绪
    canplaySong(e){
        // console.log(e)
        // console.log("jhhhhhhhh---------------")
        this.initAudioReady = true;
    },
    //音频开始播放
    playSong({target}){
      console.log("/////////////")
        console.log(target)
        this.initAudioReady = true;
        this.setPlayStatus(true);
        this.totalPlayTime = target.duration;

    },
    // 音频播放结束 根据模式切换歌曲
    endedSong (e) {
        // console.log("over------------------")
        if(this.playPattern == 1)
        return this.loopPlaySong();
        this.changPlay('next');
    },
    // 音频加载失败
    errorSong (e) {
        this.$notify({
          title: '播放提示',
          message: `《${this.curPlayInfo.name}》这首歌播放失败啦,即将切换下一首~`,
          type: 'warning',
          position: 'bottom-left',
          duration:2000
        });
        this.audioErrorTimeOut = setTimeout(() => {
            this.changPlay('next');
        }, 2000);
    },
    //音乐当前播放进度监听
    updateTime({target}){
        console.log((Array(2).join(0) + 22));


        // console.log({target})
        // console.log({audio})
        // console.log(this.initAudioReady)
        // if(!this.initAudioReady)
        // // console.log(this.initAudioReady)
        // return;
        this.curPlayTime = target.currentTime;
        console.log(this.audioProgress)
    },
    //音频进度条拖拽
    setAudioProgressive({val,flag}){
        console.log({val,flag})
        this.initAudioReady = false;
        this.curPlayTime = val * this.totalPlayTime;
        const audio = this.$refs.audio;
        if(flag){
            audio.currentTime = this.curPlayTime;
        }
    },
    //音量条拖拽
    setAudioVolume({val}){
        console.log({val})
        this.volume = val;
        const audio = this.$refs.audio;
        audio.volume = this.volume;
    },
    //切换播放模式
    changePlayPattern(){
        console.log(this.playPattern)
        if(this.playPattern == 2){
            this.playPattern = 0;
        }else{
            this.playPattern++;
        }
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.setPlayList(this.playList);
    console.log(this.curPlayInfo)
    console.log(this.playList)
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    //音量初始化
    if(this.curPlayInfo && this.playList!=null && this.playList.length>=1)
    return this.$nextTick(() =>{
                const audio = this.$refs.audio;
                audio.volume = this.volume;
            });
  },
}
</script>
