                color: #bfa;
            }
            .info{
                padding: 0 20px;
                box-sizing: border-box;
                margin: 0 auto;
                width: calc(100% - 42px);
                max-width: 1280px;
                height: inherit;
                display: flex;
                align-items: center;
                justify-content: center;
                .song-message{
                    flex: 1;
                    display: flex;
                    align-items: center;
