<!--  -->
<template>
    <div class="ranklist-headerinfo">
        <div class="img">
        <QsImg style="width:150px" :src='curTopRankData.coverImgUrl+"?param=200y200"' :lazy='false' />
        </div>
        <div class="message">
          <p class="name">{{curTopRankData.name}}</p>
          <p class="update-time">
              <label v-if="type == 1">
                <i class="el-icon-time"></i>
                &nbsp;&nbsp;
                最近更新：{{utils.dateFormat(curTopRankData.updateTime,'YYYY-MM-DD')}}
              </label>

              <label class="user-info" v-if="type == 2">
                <QsImg class="user-avatar" :src='curTopRankData.creator.avatarUrl+"?param=200y200"' :lazy='false' />
                <span class="user-name">{{curTopRankData.creator.nickname}}</span>
                {{utils.dateFormat(curTopRankData.createTime,'YYYY-MM-DD')}}
              </label>
              <br/>
              <span v-if="curTopRankData.description">({{curTopRankData.description}})</span>
          </p>
          <p class="tags" v-if="curTopRankData.tags.length">
            <router-link :to="{path:'/songsheet/songsheetmain',query:{cat:tag}}" v-for="(tag,tagIndex) in curTopRankData.tags" :key="tagIndex">
              #{{tag}}
            </router-link>
          </p>
          <p class="btn-group">
            <QsButton @port="replacePlayListAndPlay({list:songsFormat(curTopRankData.tracks)})" text='播放' icon='el-icon-video-play' />
            <QsButton @port="addSong({list:songsFormat(curTopRankData.tracks)})" text='添加' icon='el-icon-plus' />
            <QsButton :text='`${subscribed ? "已收藏" : "收藏" }(${curTopRankData.subscribedCount})`' :icon='subscribed ? "el-icon-star-on" : "el-icon-star-off"' />
            <QsButton :text='`分享(${curTopRankData.shareCount})`' icon='el-icon-share' />
            <QsButton text='下载' icon='el-icon-download' />
            <QsButton :text='`留言(${curTopRankData.commentCount})`' icon='el-icon-chat-dot-round' />
          </p>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'
import { formatSongInfo } from '@/utils/song.js'
export default {
    props:{
      curTopRankData:{
          type:Object,
          default:() => {}
      },
      subscribed:{
        type:Boolean,
        default:false
      },
      type:{
        type:Number,
        default:1
      }
    },
    components: {},
    data() {
        return {}
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
      ...mapActions('music',[
        'addSong',
        'replacePlayListAndPlay'
      ]),
      //格式化歌曲信息
      songsFormat(songs){
        let result = [];
        songs.forEach(elem => {
          result.push(formatSongInfo(elem));
        });
        return result;
      }
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {},
}
</script>
