<!--  -->
<template>
    <div class='songSheet-detail'>
        <div class="detail-info">
            <div class="background-blur">
                <QsImg class="background" :src='songSheetDetailData.backgroundCoverUrl || songSheetDetailData.coverImgUrl'/>
            </div>
            <div class="container">
                <QsImg class="thumbnail" :src='songSheetDetailData.coverImgUrl'/>
                <div class="content">
                    <p class="name">{{songSheetDetailData.name}}</p>
                    <p v-if="songSheetDetailData.creator" class="creator">
                        <QsImg class="avatar" :src="`${songSheetDetailData.creator.avatarUrl}?param=40y40`" />
                        <router-link to='/'>{{songSheetDetailData.creator.nickname}}</router-link>
                    </p>
                    <p class="play-count">
                        播放量：<span class="count"> {{utils.tranNumber(songSheetDetailData.playCount,1)}}</span>
                    </p>
                    <p class="tags" v-if="songSheetDetailData.tags.length">
                        <router-link :to="{path:'/songsheet/songsheetmain',query:{cat:tag}}" v-for="(tag,tagIndex) in songSheetDetailData.tags" :key="tagIndex">
                            #{{tag}}
                        </router-link>
                    </p>
                    <p class="bi">
                        简介：
                        <span class="text">{{songSheetDetailData.description}}</span>
                    </p>
                    <p class="btn-group">
                        <QsButton @port="replacePlayListAndPlay({list:songsFormat(songSheetDetailData.tracks)})" text='播放' icon='el-icon-video-play' />
                        <QsButton @port="addSong({list:songsFormat(songSheetDetailData.tracks)})" text='添加' icon='el-icon-plus' />
                        <QsButton :text='`${subscribed ? "已收藏" : "收藏" }(${songSheetDetailData.subscribedCount})`' :icon='subscribed ? "el-icon-star-on" : "el-icon-star-off"' />
                        <QsButton :text='`分享(${songSheetDetailData.shareCount})`' icon='el-icon-share' />
                        <QsButton text='下载' icon='el-icon-download' />
                        <QsButton :text='`留言(${songSheetDetailData.commentCount})`' icon='el-icon-chat-dot-round' />
                    </p>
                </div>
            </div>
        </div>
        <div class="container">
            <SongList :showHeaderInfo='false' tipsNum='序号' :type='2' :data='songSheetDetailData'/>
            <RelatedSongSheet :id='sid' />
            <QsComment :id='sid' :commentType='2'/>
        </div>
    </div>
</template>

<script>
import RelatedSongSheet from './related'
import { mapActions } from 'vuex'
import { formatSongInfo } from '@/utils/song.js'
export default {
    components: {
        RelatedSongSheet
    },
    inject:['reload'],
    data() {
        return {
            songSheetDetailData:{},
            subscribed:false
        }
    },
    // 监听属性 类似于data概念
    computed: {
        sid(){
            return this.$route.params.id;
        }
    },
    // 监控data中的数据变化
    watch: {
        sid(val){
            this.reload();
        }
    },
    //方法集合
    methods: {
        async getSongSheetDetailData(){
            let res = await this.$api.getSongSheetDetail(this.sid);
            this.songSheetDetailData = res.playlist;
        },
        ...mapActions('music',[
            'addSong',
            'replacePlayListAndPlay'
        ]),
        //格式化歌曲信息
        songsFormat(songs){
            let result = [];
            songs.forEach(elem => {
            result.push(formatSongInfo(elem));
            });
            return result;
        }
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.getSongSheetDetailData();
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {},
}
</script>
