<!--  -->
<template>
    <div class='songsheet-classification'>
        <LoadIng class="loading" type='4' v-if="loading" />
        <transition name="el-zoom-in-top">
            <div v-if="!loading" class="container">
                <div class="hot-cat">
                    <div class="classification-title">
                        热门歌单
                    </div>
                    <ul class="tags">
                        <li v-for="(item,index) in hotCatList" :key="index" class="tag">
                            <router-link :title="item.name" :to="{path:`/songsheet/songsheetmain`,query:{cat:item.name}}"> {{item.name}}</router-link>
                        </li>
                    </ul>
                </div>
                <div class="class-cat">
                    <div v-for="(item,index) in classAndCatList" :key="index" class="item">
                        <div class="classification-title">
                            {{item.name}}
                        </div>
                        <ul class="tags">
                            <li v-for="(tag,tIndex) in item.cats.slice(0,8)" :key="tIndex" class="tag ellipsis">
                                <router-link :title="tag" :to="{path:`/songsheet/songsheetmain`,query:{cat:tag.name}}">{{tag.name}}</router-link>
                            </li>
                            <el-popover
                                placement="bottom"
                                width="200"
                                trigger="click"
                                popper-class='popover'
                                >
                                <ul class="drop-tags">
                                    <li v-for="(tag,tIndex) in item.cats.slice(9)" :key="tIndex" class="drop-tag ellipsis">
                                        <router-link :title="tag" :to="{path:`/songsheet/songsheetmain`,query:{cat:tag.name}}">{{tag.name}}</router-link>
                                    </li>
                                </ul>
                                <li v-if="item.cats.length > 9" slot="reference" class="tag ellipsis">
                                    <router-link :to="{path:`/songsheet/songsheetmain`,query:{cat:item.cats.slice(9)[0].name}}">{{item.cats.slice(9)[0].name}}<i class="el-icon-arrow-down"></i> </router-link>
                                </li>
                            </el-popover>
                        </ul>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>

export default {
  components: {},
  data() {
    return {
        hotCatList:[],
        classAndCatList:[],
        loading:true
    }
  },
  // 监听属性 类似于data概念
  computed: {
    curCat(){
        return this.$route.query.cat;
    }
  },
  // 监控data中的数据变化
  watch: {
    curCat(val){
        this.$emit('postCat',val);
    }
  },
  //方法集合
  methods: {
    async getSongSheetCatList(){
        this.loading = true;
        let res = await this.$api.getSongSheetCatList();
        this.hotCatList = res.sub.filter(val => val.hot);
        let categories = res.categories;
        let sub = res.sub;
        // console.log(sub)
        Object.keys(categories).forEach(key =>{
            this.classAndCatList.push({name:categories[key],cats:[]});
            this.$set(this.classAndCatList[key],'cats',sub.filter(Sval => Sval.category == key && !Sval.hot));
        });

        console.log(this.classAndCatList)
        this.loading = false;
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getSongSheetCatList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
}
</script>
