<!--  -->
<template>
  <div class='video-detail'>
    <div class="container">
      <div class="video-container">
        <el-row class="video-box">
          <el-col id="dplayer" :span="videoCol[0]" class="left-video">
          </el-col>
          <el-col :span="videoCol[1]" class="right-list">
          </el-col>
        </el-row>
        <el-row class="content">
          <el-col :xl="12" :lg="12" :md="12" :sm='24' :xs='24' class="left-msg">
            <div class="title ellipsis">
              {{mvDetail.name}}
            </div>
            <div class="message">
              播放量：{{mvDetail.playCount || 0}}
              <router-link v-for="(item,index) in mvDetail.artists" :key="index" class="singer" :to="`/singer/detail/${item.id}`">/&nbsp; {{item.name}} </router-link>
              <span class="date">发布时间：{{mvDetail.publishTime || '无'}}</span>
            </div>
          </el-col>
          <el-col :xl="12" :lg="12" :md="12" :sm='24' :xs='24' class="right-btngroup">
            <ul class="action-container">
              <li @click="SubVideo" :class="[isLiked ? 'active' : '']" class="item sub">
                <i class="iconfont icon-shoucang_huaban1"></i>
                <span class="text">{{isLiked ? '已收藏' :'收藏'}}</span>
              </li>
              <li class="item">
                <i class="iconfont icon-fenxiang1"></i>
                <span class="text">分享</span>
              </li>
              <el-popover
                placement="bottom"
                :title="`收藏:${mvDetail.subCount} / 分享:${mvDetail.shareCount} / 评论:${mvInfo.commentCount}`"
                width="300"
                trigger="hover"
                popper-class='desc-popper'
                :content="`介绍：${mvDetail.desc || '无'}`"
              >
                <li slot="reference" class="item">
                  <i class="iconfont icon-xiangxi"></i>
                  <span class="text">更多信息</span>
                </li>
              </el-popover>

            </ul>
          </el-col>
        </el-row>
      </div>
      <div class="relevant-title">
        相关视频推荐
      </div>
      <VideoList theme='dark' :videoList='relevantVideoData.slice(0,5)' />
      <QsComment theme='dark' style="margin:35px auto;" :id='mvId' :commentType='1'/>
    </div>
  </div>
</template>

<script>
import DPlayer from 'dplayer';
import VideoList from '../main/video-list'
export default {
  components: {
    VideoList
  },
  inject:['reload'],
  data() {
    return {
      dp:null,
      videoCol:[24,0],
      mvDetail:{artists:[{name:''}]},//mv详情数据
      relevantVideoData:[],
      mvInfo:{},//mv信息 包括是否收藏
    }
  },
  // 监听属性 类似于data概念
  computed: {
    mvId(){
      return this.$route.params.id;
    },
    //是否收藏
    isLiked(){
      return this.mvInfo.liked ? true : false;
    }
  },
  // 监控data中的数据变化
  watch: {
    mvId(){
      this.reload();
    }
  },
  //方法集合
  methods: {
    async getMvDetailData(){
      let res = await this.$api.getMvDetail(this.mvId);
      this.mvDetail = res.data;
    },
    async getMvUrl(){
      let res = await this.$api.getMvUrl(this.mvId);
      this.dp = new DPlayer({
          container: document.getElementById('dplayer'),
          autoplay:true,
          video: {
            url: res.data.url
          },
      });
    },
    async getRelevantVideo(){
      let res = await this.$api.getRvMv(this.mvId);
      this.relevantVideoData = res.mvs;
    },
    async getMvInfo(){
      let res = await this.$api.getMvInfo(this.mvId);
      this.mvInfo = res;
    },
    async SubVideo(){
      let res = await this.$api.subMvideo({mvid:this.mvId,t:this.isLiked ? 0 : 1});
      if(res.code == 200){
        return this.$set(this.mvInfo,'liked',!this.isLiked);
      }
    },
    reloadData(){
      this.getMvDetailData();
      this.getMvUrl();
      this.getRelevantVideo();
      this.getMvInfo();
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.reloadData();
  },
}
</script>
