<!--  -->
<template>
  <QsButton :class="[noMoreStyle]" class="loadmove-btn" :icon="loading ? 'el-icon-loading' : '' " :text="loading ? '加载中...' : move ? '加载更多' :'已经到底啦~' "/>
</template>
<script>
export default {
  props:{
    loading:{
      type:Boolean,
      default:false
    },
    move:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {}
  },
  // 监听属性 类似于data概念
  computed: {
    noMoreStyle(){

      return !this.move ? 'noMore' : ''
    }
  },
  // 监控data中的数据变化
  watch: {},
  //方法集合
  methods: {},
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
}
</script>
