/* 
    获取模块评论 
    type:(0: 歌曲 1: mv 2: 歌单 3: 专辑 4: 电台 5: 视频6: 动态) 
    pageNo:分页参数,第N页,默认为1
    pageSize:分页参数,每页多少条数据,默认20
    sortType: 排序方式,1:按推荐排序,2:按热度排序,3:按时间排序 
*/
export const getComment = params => api.get(`/comment/new`,{params});

/* 
    获取模块热门评论
    type:同上 
    limit：limit: 取出评论数量 , 默认为 20
    offset：偏移数量 , 用于分页 , 如 :( 评论页数 -1)*20, 其中 20 为 limit 的值
*/
export const getHotComment = params => api.get(`/comment/hot`,{params});

