<template>
    <div :class="[theme]" class='qs-comment'>
        <div class="title">
            用户评论
        </div>
        <div data-v-60867bb4="" style="width: 100%; height: 2px; background: rgb(255, 99, 72);"></div>
        <div class="form">
            <el-input rows='4' placeholder="请留下您的精彩评论~" class="textarea" type="textarea" v-model="commentForm.content"></el-input>
            <QsButton @port="comment" class="btn" text='发表评论' />
        </div>
        <div class="container">
            <div class="comment-type-title">
                热门评论({{hotCommentData.total}})
            </div>
            <div v-for="(list,index) in hotCommentData.hotComments" :key="index" class="media">
                <div class="media-left">
                    <QsImg class="img" :src='list.user.avatarUrl' />
                </div>
                <div class="media-body">
                    <div class="info" >
                        <router-link :title="list.user.nickname" class="name ellipsis" :to="`/`">{{list.user.nickname}}</router-link>
                        <span class="date">{{utils.dateFormat(list.time,'YYYY-MM-DD HH:MM:SS')}}</span>
                    </div>
                    <div :title="list.content" class="text ellipsis-two">
                        {{list.content}}
                    </div>
                    <div class="btn-group">
                        <a @click="commentLike(list,'hot')" :class="list.liked ? 'liked':''"> <i class="icon iconfont icon-like"></i>&nbsp; {{list.likedCount}}</a>
                        <a href="#"><i class="icon el-icon-chat-dot-round"></i></a>
                    </div>
                </div>
            </div>
            <div class="comment-type-title">
                全部评论({{allCommentData.totalCount}})
            </div>
            <div v-for="list in allCommentData.comments" :key="list.id" class="media">
                <div class="media-left">
                    <QsImg class="img" :src='list.user.avatarUrl' />
                </div>
                <div class="media-body">
                    <div class="info" >
                        <router-link :title="list.user.nickname" class="name ellipsis" :to="`/`">{{list.user.nickname}}</router-link>
                        <span class="date">{{utils.formatMsgTime(list.time)}}</span>
                    </div>
                    <div :title="list.content" class="text ellipsis-two">
                        {{list.content}}
                    </div>
                    <div class="btn-group">
                        <a @click="commentLike(list,'all')" :class="list.liked ? 'liked':''"> <i class="icon iconfont icon-like"></i>&nbsp; {{list.likedCount || 0}}</a>
                        <a href="#"><i class="icon el-icon-chat-dot-round"></i></a>
                    </div>
                </div>
            </div>
            <div class="pagination">
                <el-pagination
                    @current-change='curChange'
                    :page-size='params.pageSize'
                    :background='theme == "dark"'
                    layout="prev, pager, next"
                    :total="allCommentData.totalCount"
                    :current-page="params.pageNo"
                >
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    props:{
        commentType:{
            type:Number,
            default:0
        },
        id:{
            type:String,
            default:''
        },
        theme:{
            type:String,
            default:''
        }
    },
    data() {
        return {
            params:{
                id:this.id,
                type:this.commentType,
                sortType:3,
                pageNo:1,
                pageSize:20
            },
            commentForm:{
                t:1,
                type:this.commentType,
                id:this.id,
                content:''
            },
            allCommentData:{
                comments:[],
                totalCount:0
            },
            hotCommentData:{
                hotComments:[],
                total:0
            },
            cursor:null
        }
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {
        'params.sortType'(){
            this.getAllComment();
        }
    },
    //方法集合
    methods: {
        async getHotComment(){
            let {id,type} = this.params;
            let res = await this.$api.getHotComment({id,type});
            // console.log(res)
            let {total,hotComments} = res;
            this.$set(this.hotCommentData,'total',total);
            this.$set(this.hotCommentData,'hotComments',hotComments);
            console.log(this.hotCommentData)

        },
        async getAllComment(){
            let res = await this.$api.getComment(this.params);
            console.log(res)
            let {totalCount,comments,cursor} = res.data;

            this.$set(this.allCommentData,'totalCount',totalCount);
            this.$set(this.allCommentData,'comments',comments);
            // console.log(this.allCommentData)
            this.cursor = cursor;
            console.log(this)
        },
        curChange(val){
            console.log(this.params)
            this.$set(this.params,'pageNo',val);
            console.log(this.params)



            if(val!=1){
                this.$set(this.params,'cursor',this.cursor);
            }else{
                delete this.params.cursor;
            }
            this.getAllComment();
        },
        async commentLike({commentId,liked,likedCount},commentstType){
            let params = {id:this.id,cid:commentId,t:liked === false ? 1 : 0,type:this.commentType};
            let res = await this.$api.commentLike(params);
            if(res.code == 200){
                if(commentstType == 'hot'){
                    console.log(val)
                    let commentIndex = this.hotCommentData.hotComments.findIndex(val => commentId === val.commentId );
                    this.$set(this.hotCommentData.hotComments[commentIndex],'liked',!liked);
                    this.$set(this.hotCommentData.hotComments[commentIndex],'likedCount',liked == false ? (likedCount+1) : (likedCount-1));
                }else if(commentstType == 'all'){
                    let commentIndex = this.allCommentData.comments.findIndex(val => commentId === val.commentId );
                    this.$set(this.allCommentData.comments[commentIndex],'liked',!liked);
                    this.$set(this.allCommentData.comments[commentIndex],'likedCount',liked == false ? (likedCount+1) : (likedCount-1));
                }
            }
        },
        comment(){
            if(this.commentForm.content == '')
            return this.$message.warning('请填写您的评论内容~');
            this.$api.comment(this.commentForm).then(res =>{
                this.allCommentData.comments.unshift(res.comment);
                this.allCommentData.totalCount++;
                this.$message.success('评论成功~');
                this.commentForm.content = '';
            });
        }

    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.getHotComment();
        this.getAllComment();

    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {},
}
</script>
