<!--  -->
<template>
  <div :style="{height:bannerHeight}" class="banner-component">
        <div v-loading="bannerData.length <1" class="swiper-container">
            <div class="swiper-wrapper">
                <div @click="goToUrl(item.url)" v-for="(item,index) in bannerData" :key="index" class="swiper-slide">
                    <div class="img-container">
                        <el-image
                            class="image"
                            :src="item[IMGKEY]"
                            fit="cover"
                        >
                            <div slot="error" class="image-slot">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                        <!-- <div :style="{background:`url(${item[IMGKEY]}) no-repeat center`}" class="image">
                        </div> -->
                    </div>
                </div>
            </div>
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
import Swiper from 'swiper'
export default {
    props:{
        slidesPerView:{
            type:String,
            default:'5'
        },
        bannerHeight:{
            type:String,
            default:'auto'
        },
        bannerData:{
            type:Array,
            default:() => []
        },
        IMGKEY:{
            type:String,
            default:'imageUrl'
        }
    },
    components: {},
    data() {
        return {}
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {
        'bannerData.length'(val){
            if(val >=1){
                this.loadSwiper();
            }
        }
    },
    //方法集合
    methods: {
        goToUrl(url){
            if(!url) return;
            window.open(url);
        },
        loadSwiper(){
            let that = this;
            this.$nextTick(e =>{
                var mySwiper = new Swiper('.swiper-container', {
                    slidesPerGroup: 3,
                    autoplay: true,//可选选项，自动滑动
                    slidesPerView: that.slidesPerView,
                    pagination: {
                    el: '.swiper-pagination',
                    },
                    // navigation: {
                    //     nextEl: '.swiper-button-next',
                    //     prevEl: '.swiper-button-prev',
                    // },
                });
            });
        },
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
    },
}
</script>
<style lang='less' scoped>
//@import url(); 引入公共css类
    .banner-component{
        position: relative;
        .swiper-container{
            .swiper-wrapper .swiper-slide{
                cursor: pointer;
                .img-container{
                    padding: 0 10px;
                    box-sizing: border-box;
                    width: 100%;
                    // height: 100%;
                    .image{
                        border-radius: 5px;
                        width: 100%;
                        // height: 100%;
                    }
                }
            }
        }
    }
</style>