
//合并歌曲列表,只有传入数组才能用拓展运算符所以传参 playChoiceSong({list:[list]})
const playListconcat = (list,playList = []) =>{
    const arr = [...playList, ...list];
    const map = new Map();
    // console.log(map)
    for (const item of arr) {
        if (!map.has(item.id)) {
            map.set(item.id, item)
        }
    }
    return [...map.values()];
}
export default {
    namespaced: true,
    state:{
        isPlay:false,
        playList:[],
        playIndex:0
    },
    getters:{
        isPlay(state){
            return state.isPlay;
        },
        playList(state){
            return state.playList.length ? state.playList : JSON.parse(window.localStorage.getItem('playList')) || []
        },
        playIndex(state){
            return state.playIndex || window.localStorage.getItem('playIndex') || 0
        },
        curPlayInfo(state){
            return state.playList[state.playIndex]
        }
    },
    mutations:{
        setPlayStatus(state,val = false){
            state.isPlay = val;
        },
        setPlayList(state,val = []){
            state.playList = val;
            window.localStorage.setItem('playList',JSON.stringify(val))
        },
        setPlayIndex(state,val = 0){
            console.log(val)
            state.playIndex = val;
            window.localStorage.setItem('playIndex',val)
        }
    },
    actions:{
        //添加并播放当前选择的歌曲
        playChoiceSong({commit,state},{list}){
            console.log(list)
            commit('setPlayList',playListconcat(list,state.playList));
            console.log("****")
            commit('setPlayIndex',state.playList.findIndex( ele => list[0].id === ele.id ));
            console.log("------------------------")
        },
        //添加音乐到播放列表
        addSong({commit,state},{list}){
            commit('setPlayList',playListconcat(list,state.playList));
        },
        //替换当前列表所有音乐到播放列表并播放
        replacePlayListAndPlay({commit},{list}){
            commit('setPlayList',playListconcat(list,[]));
            commit('setPlayIndex',0);
        },
        //对播放列表的歌曲进行删除
        removePlayListSong({commit,state},index){
            console.log(index + '/' + state.playIndex);
            let playList = state.playList;
            playList.splice(index,1);
            commit('setPlayList',playList);
            if( index <= state.playIndex && index !=0 ){
                commit('setPlayIndex',(state.playIndex-1));
            }else{
                commit('setPlayIndex',(state.playIndex++));
            }
        }
    }
}