<!--  -->
<template>
  <div @click.stop :class="[show]" class='mobile-Side-nav'>
      <div class="container">
        <div
            :class="{'item-active':activeIndexNum == index}"
            @click="changeNavAndGoToRoute(item,index)"
            v-for="(item,index) in navs"
            :key="index"
            class="navs"
        >
            {{item.name}}
          </div>
      </div>
  </div>
</template>

<script>

export default {
  components: {},
  data() {
    return {
        navs:[
            {name:'发现音乐',routePath:'findmusic',routeName:'findmusic'},
            {name:'排行榜',routePath:'ranking',routeName:'ranking'},
            {name:'歌单',routePath:'songsheet',routeName:'songsheetmain'},
            {name:'视频',routePath:'video?cat=全部&type=全部',routeName:'videomain'},
            {name:'歌手',routePath:'singer?area=-1&type=-1&initial=-1',routeName:'singerMain'},
            {name:'我的音乐',routePath:'mymusic',routeName:'mymusic'},
            // {name:'动态',routePath:'dynamic',routeName:'dynamic'},
            // {name:'电子冲脉',routePath:'electronpulse',routeName:'electronpulse'}
        ],
        activeIndexNum:0
    }
  },
  // 监听属性 类似于data概念
  computed: {
    show(){
        return this.$store.getters.showMobile ? 'show' : '';
    }
  },
  // 监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    changeNavAndGoToRoute(data,activeNum){
        this.activeIndexNum = activeNum;
        if(this.$route.name != data.routeName){
            this.$router.push(`/${data.routePath}`);
            this.$store.commit('changeMobileShow',false);
        }
    },
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.activeIndexNum = this.navs.findIndex(val => this.$route.name == val.routeName);
  },
}
</script>
<style lang='less' scoped>
//@import url(); 引入公共css类
    .mobile-Side-nav{
        box-sizing: border-box;
        position: fixed;
        bottom: 0;
        left: 0;
        width: 32vw;
        height: calc(100% - 50px);
        background: #57606f;
        color: #fff;
        transition: .3s ease-out;
        transform:translateX(-100%);
        opacity: 0;
        &.show{
            transform:translateX(0);
            opacity: 1;
        }
        .container{
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content:flex-start;
            .navs{
                padding: 0 10px;
                box-sizing: border-box;
                line-height: 32px;
                border-radius: 32px;
                margin-top: 1.2em;
                font-size: 14px;
                // transition: .2s ease-out;
                &.item-active{
                    background:#ff4757;
                }
            }
        }
    }
</style>