<!--  -->
<template>
  <div class='mymusic'>
    <el-row class="container">
        <el-col class="left-top"  :xl='6' :lg='6' :md='6' :sm='24' :xs='24'>
            <div class="my-subscribesinger row">
                <div @click="shouSingerList" :class="['title']">
                    <span>我收藏的歌手({{singerCount}})</span>
                </div>
            </div>
            <div class="my-create row">
                <div @click="slideListOne = !slideListOne" :class="['title',slideListOne ? 'active' : '' ]">
                    <span>我创建的歌单({{createList.length}})</span>
                    <i class="icon el-icon-caret-right"></i>
                </div>
                <LoadIng type='4' v-if="leftLoading" />
                <SongSheetList :type='2' v-show="slideListOne&&!leftLoading" :songSheetList='createList' :topActiveNum='topActiveNum' @changeToprank='changeToprank' />
            </div>
            <div class="my-subscribe row">
                <div @click="slideListTow = !slideListTow" :class="['title',slideListTow ? 'active' : '' ]">
                    <span>我收藏的歌单({{subscribeList.length}})</span>
                    <i class="icon el-icon-caret-right"></i>
                </div>
                <SongSheetList :type='2' v-show="slideListTow&&!leftLoading" :songSheetList='subscribeList' :topActiveNum='topActiveNum' @changeToprank='changeToprank' />
            </div>
        </el-col>
        <el-col class="right-container" :xl='18' :lg='18' :md='18' :sm='24' :xs='24'>
            <LoadIng class="loading" type='4' v-if="rightInfoLoading" />
            <transition name="el-zoom-in-top">
                <div v-show="!rightInfoLoading && !slideSinger">
                    <SongSheetDetailHadInfo :type='2' :subscribed='subscribed' :curTopRankData='curTopRankData' />
                    <SongList :type='2' style="padding:0 40px;box-sizing: border-box;" :data='curTopRankData'/>
                </div>
            </transition>
            <transition name="el-fade-in-linear">
                <SingerList v-if="slideSinger" @curChange='getSubscribedSinger' :singerList='singerListData' :singerCount='singerCount' />
            </transition>
        </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  components: {
    SingerList:() => import(/*webpackChunkName:"singList"*/'@/components/singerList')
  },
  data() {
    return {
        createList:[],//创建歌单
        subscribeList:[],//收藏歌单
        slideSinger:false,//显示我收藏的歌手列表
        slideListOne:true,//展开创建歌单
        slideListTow:false,//展开收藏歌单
        leftLoading:true,
        curTopRankData:{},
        topActiveNum:null,
        //右侧loading控制
        rightInfoLoading:true,
        subscribed:false,
        singerListData:[],
        singerCount:0
    }
  },
  // 监听属性 类似于data概念
  computed: {
    ...mapGetters('user',[
        'userInfo'
    ])
  },
  // 监控data中的数据变化
  watch: {
  },
  //方法集合
  methods: {
    //获取我的歌单(创建、收藏)
    async getMyMusicData(){
        this.leftLoading = true;
        let res = await this.$api.getUserSongSheet(this.userInfo.userId);
        this.createList = res.playlist.filter(val => !val.subscribed);
        this.subscribeList = res.playlist.filter(val => val.subscribed);
        this.leftLoading = false;
        this.changeToprank(this.createList[1].id);
    },
    //获取歌单详情
    async changeToprank(id){
      if(this.topActiveNum == id) return;
      this.slideSinger = false;//点击歌单详情时，隐藏歌手列表
      this.topActiveNum = id;
      this.rightInfoLoading = true;
      let res = await this.$api.getSongSheetDetail(id);
      this.getSongSheetDynamic(id);
      this.rightInfoLoading = false;
      this.curTopRankData = res.playlist;
    },
    //获取歌单动态 是否收藏
    async getSongSheetDynamic(id){
      let res = await this.$api.getSongSheetDynamic(id);
      this.subscribed = res.subscribed;
      console.log(res)
      console.log(this.subscribed)
    },
     //获取我收藏的歌手
    async getSubscribedSinger(offset = 0,limit = 18){
        let res = await this.$api.getUsersubscribedSinger({offset,limit});
        console.log(res)
        this.singerListData = res.data;
        this.singerCount = res.count ? res.count : this.singerCount;
    },
    //显示歌手列表页并清除歌单列表激活状态
    shouSingerList(){
        if(this.slideSinger) return;
        this.slideSinger = true;
        this.topActiveNum = null;
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getMyMusicData();
    this.getSubscribedSinger();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
}
</script>
