                        line-height: 50px;
                        font-size: 12px;
                        font-weight: 700;
                        cursor: pointer;
                        transition: .2s;
                        &.active{
                            .icon{
                                transform: rotate(90deg);
                            }
                        }
                        .icon{
                            font-size: 16px;
                            font-weight: normal;
                            transition: .2s;
                        }
                    }
                }
            }
            .right-container{
                padding: 10px 10px;
                box-sizing: border-box;
                position: relative;
                .loading{
                    position: absolute;
