<!--  -->
<template>
  <div class='component-songSheet'>
    <div class="homecmp-title">
        {{title}}
        <div class="cat-item">
            <div @click="catChange(item,index)" :class="activeNum == index ? 'active' : '' " v-for="(item,index) in catList" :key="index" class="item">
                {{item}}
            </div>
        </div>
    </div>
    <LoadIng v-if="loading" />
    <div v-if="!loading" class="songsheet-container">
        <div v-for="(item,index) in songSheetDataList" :key="index" class="list">
            <div @click="$router.push(`/songsheet/songsheetdetail/${item.id}`)" class="songsheet-img">
                <QsImg
                    class="songsheet-image"
                    :style="{'max-height':'135px'}"
                    :src=" item.coverImgUrl + '?param=300y300'"
                />
                <QsImg class="creator-avatar" :src="item.creator.avatarUrl">
                </QsImg>
            </div>
            <p @click="$router.push(`/songsheet/songsheetdetail/${item.id}`)" class="text ellipsis-two">
                {{item.name}}
            </p>
            <div class="hot-msg">
                {{utils.tranNumber(item.playCount,2)}}
                <i class="icon el-icon-sort-up"></i>
            </div>
            <div class="creator ellipsis">
                by <span class="creator-name" >{{item.creator.nickname}}</span>
            </div>
        </div>
    </div>
  </div>
</template>

<script>

export default {
    props:{
        songSheetDataList:{
            type:Array,
            default:() => []
        },
        title:{
            type:String,
            default:'推荐歌单'
        },
        loading:{
            type:Boolean,
            default:false
        }
    },
    components: {},
    data() {
        return {
            catList:[
                '全部','流行','华语','欧美','韩语','日语','电子'
            ],
            activeNum:0
        }
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        catChange(val,index){
            if(this.activeNum == index)
            return;
            this.activeNum = index;
            this.$emit('songSheetCatChange',val);
        }
    },
    // 生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
    },
}
</script>
