<!--  -->
<template>
  <div class='qs-input'>
    <input v-model="keywords" @focus="$emit('focus')" placeholder="搜索歌曲、歌手、专辑" type='text' name="" required="" />
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  components: {},
  data() {
    return {
      keywords:''
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {
    keywords(val){
      this.changeSearchVal(val);
    }
  },
  //方法集合
  methods: {
    ...mapMutations([
      'changeSearchVal'
    ])
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
}
</script>
